

.headerContainer {
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    height:8vh;
    align-items: center;
    background-color:#0a192f;
}

.navContainer {
    display: flex;
    flex-direction: row;
    width: 50vh;
    margin-right: 5vh;
    justify-content: space-around;
    align-items: center;
    list-style-type: none;
    color: white;
}

.navContainer li {
    display: inline;
    padding-left: 10px;
    padding-right: 10px;
}

.navContainer li:hover {
    color: #64FFDA;
    cursor: pointer;
}

.logoContainer {
    margin-left: 10vh;
    color: #64FFDA;
}

.index {
    color: #64FFDA;
}